@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
body {
  background: linear-gradient(
    90deg,
    hsla(278, 35%, 78%, 1) 0%,
    hsla(337, 100%, 89%, 1) 20%,
    hsla(338, 100%, 84%, 1) 40%,
    hsla(208, 97%, 87%, 1) 60%,
    hsla(209, 100%, 82%, 1) 80%
  );
  font-family: "Indie Flower", cursive;
  color: #ffffff;
  overflow: hidden;
}

:root {
  --button-background: rgb(255, 232, 232);
}

.main {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.btn {
  width: 10rem;
  height: 4rem;
  font-size: 1.4rem;
  font-family: "Indie Flower", cursive;
  background: rgb(255, 232, 232) content-box;
  background: var(--button-background) content-box;
  border-radius: 2rem;
  border: 3px dotted rgb(255, 232, 232);
  border: 3px dotted var(--button-background);
  padding: 5px;
  cursor: pointer;
}

.quote {
  padding: 1em;
  text-align: center;
  text-shadow: 2px 3px 4px #000000;
  font-size: 1.2rem;
}

